.blue-back {
  background-image: url('https://s3.amazonaws.com/seedlaw-photos/blue.jpg');
  background-position: center;
  background-size: cover;
  min-height:100vh;
}

.display-top {
  padding-top: 12rem;
}

.quote-container {
  display: block;
  width: 100%;
  max-width: 78.75rem;
  margin-right: auto;
  margin-left: auto;
  padding-right: 4rem;
  padding-left: 4rem;
}

.inner-contain {
  position: relative;
  padding-bottom: 20rem;
}

.hero {
  position: relative;
  width: 31.5rem;
}

.hero-quote {
  position: relative;
  z-index: 12;
  color: #09194c;
  padding-top: 2rem;
}

.stack-1 {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  padding-right: 2rem;
  padding-bottom: 2rem;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
}

h1 {
  margin-top: 0px;
  margin-bottom: 0px;
  font-family: Akrobat, sans-serif;
  font-size: 32px;
  line-height: 30px;
  font-weight: 400;
  text-transform: uppercase;
}

.hero-image {
  position: absolute;
  left: 31rem;
  top: 0px;
  z-index: 123;
  width: 31rem;
  height: 31rem;
}

.image-contain {
  position: relative;
  display: inline-block;
  overflow: hidden;
  border-radius: 50%;
  box-shadow: 0 0 32px 0 rgba(9, 25, 76, .2);

}

img {
  width: 300px;
}

p {
  color: #fff;
  font-family: Akrobat, sans-serif;
  font-size: 25px;
  line-height: 31px;
  font-weight: 400;
}

@media(max-width: 767px) {
  .quote-container {
    padding-right: 1rem;
    padding-left: 1rem;
  }

  .hero {
    width: 90vh;
  }
}
